import Home from './Home';
import Doc from './Doc';
import Tuto from './Tuto';
import Blog from './Blog';
import Comm from './Comm';
import Login from './Login';
import Core from './Core';
import Guide from './Guide';
import Api from './Api';
import Hook1 from './Hook1';

const routes = [
    {
        path: '/',
        exact: true,
        component: Home
    },
    {
        path: '/doc',
        component: Doc,
        pri: true,
        routes: [{
            path: '/doc/core',
            component: Core
        },
        {
            path: '/doc/guide',
            component: Guide
        },
        {
            path: '/doc/api',
            component: Api
        },
        {
            path: '/doc/hook1',
            component: Hook1
        }
        ]
    },
    {
        path: '/tuto',
        component: Tuto,
        pri: true

    },
    {
        path: '/blog',
        component: Blog
    },
    {
        path: '/comm',
        component: Comm
    },
    {
        path: '/login',
        component: Login
    },

]
export default routes;