import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Header from './pages/Header';
import routes from './pages/router.config';


function PrivateRoute({ component: Com, ...rest }) {
	return (
		<Route
			{...rest}
			render={({ location }) =>
				localStorage.getItem('userinfo')
					? <Com {...rest} />
					: (
						<Redirect
							to={{
								pathname: "/login",
								state: location
							}}
						/>
					)
			}
		/>
	);
}
function RouteWithSubRoutes(route) {
	console.log(route)
	if (route.pri) {
		return (
			<PrivateRoute
				path={route.path}
				routes={route.routes} component={route.component}
			/>
		)
	}
	return (
		<Route
			path={route.path}
			render={props => (
				// pass the sub-routes down to keep nesting
				<route.component {...props} />
			)}
		/>
	);
}
const App = (props) => {

	return <Router>
		<Header />
		<Switch>
			{routes.map((route, i) => (
				<RouteWithSubRoutes key={i} {...route} />
			))}
		</Switch>
	</Router>
}

export { App, RouteWithSubRoutes };
