//NAvbar导航栏10.12 14点41分
import React, { useEffect } from 'react'
import { Link, Route, Redirect, useHistory, useLocation, useParams, useRouteMatch, withRouter } from 'react-router-dom'
import "./Doc.css"
import { RouteWithSubRoutes } from '../App';


const Doc = (props) => {
    return (
        <div className='doc'>

            <div className='content'>
                {
                    props.routes.map(
                        (route, i) => (
                            <RouteWithSubRoutes key={i} {...route} />
                        )

                    )}
                <Redirect from='/doc' to='/doc/core' />


            </div>
            <ol>
                <li><Link activeClassName="active" to="/doc/core">核心概念</Link></li>
                <li><Link activeClassName="active" to="/doc/guide">高级指引</Link></li>
                <li><Link activeClassName="active" to="/doc/api">API</Link></li>
                <li><Link activeClassName="active" to="/doc/hook1">Hook</Link></li>


            </ol>
        </div>
    )
}
export default Doc;


{/* <Route exact path='/' component={Home} >核心概念</Route> */ }
{/* <Route path='/doc/core' component={Core} ></Route>
                <Route path='/doc/guid' component={DocContent} />
                <Route path='/api' component={DocContent}/> */}
{/* <Route path='*' component={()=><div>404</div>}> </Route>
                嵌套路由
                <Route path='/doc/:type' component={DocContent}></Route>
                默认进来的时候没有参数，就转到这个地方
                <Redirect from='/doc' to='/doc/core' /> */}

